import Branch from './Branch';
import * as THREE from 'three';
import { TextGeometry } from 'three/addons/geometries/TextGeometry.js';
import { randInt } from 'modules/utils.js';
import fragmentShader from './fragmentShader.js';
import vertexShader from './vertexShader.js';
import TWEEN from '@tweenjs/tween.js';

const mainImageGeometry = new THREE.PlaneGeometry(20, 20);

class Node {
	constructor(mapNode, index, parent, map) {
		this.mapNode = mapNode;
		this.map = map;
		this.index = index;
		this.parent = parent;
		this.textureLoader = new THREE.TextureLoader();
	}

	async init() {
		await this.loadMainImageTexture();
		this.createMainImageMesh();
		this.createMainTextMesh();
		await this.createBranches();
	}

	loadMainImageTexture() {
		return new Promise((resolve) => {
			this.textureLoader.load(this.mapNode.mainImage, (texture) => {
				this.texture = texture;
				resolve();
			});
		});
	}

	getAspectRatio() {
		return this.texture.image.width / this.texture.image.height;
	}

	createMainImageMesh() {
		const material = new THREE.ShaderMaterial({
			side: THREE.DoubleSide,
			uniforms: {
				uTexture: { value: this.texture },
				uTime: { value: 0 },
				uAmp: { value: 0 },
			},
			vertexShader,
			fragmentShader,
		});

		const mesh = new THREE.Mesh(mainImageGeometry, material);
		mesh.position.copy(this.mapNode.position);

		const aspectRatio = this.getAspectRatio();
		mesh.scale.set(aspectRatio, 1, 1);

		this.parent.scene.add(mesh);
		this.mesh = mesh;
	}

	createMainTextMesh() {
		const text = Object.keys(this.map)[this.index];
		const textGeometry = new TextGeometry(text, {
			font: this.parent.font,
			size: 1,
			height: 0.1,
			curveSegments: 12,
			bevelEnabled: false,
		});

		const textMaterial = new THREE.MeshBasicMaterial({
			color: 0xffffff,
			opacity: 0,
			transparent: true,
			side: THREE.DoubleSide,
		});

		const aspectRatio = this.getAspectRatio();

		let textMesh = new THREE.Mesh(textGeometry, textMaterial);

		textMesh.position.copy(this.mapNode.position);

		textMesh.position.y -= 14;
		textMesh.position.x -= aspectRatio * 5;

		this.parent.scene.add(textMesh);
		this.textMesh = textMesh;
	}

	onAnimate(t) {
		const cameraPos = this.parent.camera.position;

		const mesh = this.mesh;

		if (mesh) {
			mesh.lookAt(cameraPos);
			mesh.material.uniforms.uTime.value = t / 1000;

			const aspectRatio = this.getAspectRatio();

			// if (this.parent.selectedNode) return;

			if (
				this.parent.intersects.length > 0 &&
				this.parent.intersects[0].object === this.mesh
			) {
				if (this.mesh.material.uniforms.uAmp.value < 20) {
					this.mesh.material.uniforms.uAmp.value += 0.01;
					document.body.style.cursor = 'pointer';

					if (this.mesh.scale.x < 1.1 * aspectRatio) {
						new TWEEN.Tween(this.mesh.scale)
							.to(
								{
									x: aspectRatio * 1.1,
									y: 1 * 1.1,
									z: 1 * 1.1,
								},
								100
							)
							.easing(TWEEN.Easing.Quadratic.Out)
							.start();
					}

					if (this.textMesh.material.opacity < 1) {
						new TWEEN.Tween(this.textMesh.material)
							.to({ opacity: 1 }, 100)
							.easing(TWEEN.Easing.Quadratic.Out)
							.start();
					}

					if (this.parent.mouseDown) {
						this.parent.mouseDown = false;
						this.parent.selectedNode = this;

						this.parent.onClick(this);
					}
				}
			} else if (this.mesh.material.uniforms.uAmp.value > 0) {
				this.mesh.material.uniforms.uAmp.value -= 0.05;
				document.body.style.cursor = 'default';

				if (this.mesh.scale.x > aspectRatio) {
					new TWEEN.Tween(this.mesh.scale)
						.to({ x: aspectRatio, y: 1, z: 1 }, 100)
						.easing(TWEEN.Easing.Quadratic.Out)
						.start();
				}

				if (this.textMesh.material.opacity > 0) {
					new TWEEN.Tween(this.textMesh.material)
						.to({ opacity: 0 }, 100)
						.easing(TWEEN.Easing.Quadratic.Out)
						.start();
				}
			}
		}

		if (this.textMesh) {
			this.textMesh.lookAt(cameraPos);
		}

		if (this.branches) {
			for (const branch of this.branches) {
				branch.onAnimate(t);
			}
		}
	}

	createBranches() {
		if (!this.mapNode.branches) return;

		this.branches = [];

		return new Promise(async (resolve) => {
			for (const mapBranch of Object.values(this.mapNode.branches)) {
				const branch = new Branch(mapBranch, this.parent, this);
				await branch.init();

				this.branches.push(branch);
			}

			resolve();
		});
	}
}

export default Node;
