import * as THREE from 'three';
import { TextGeometry } from 'three/addons/geometries/TextGeometry.js';
import { randInt } from 'modules/utils.js';
import TWEEN from '@tweenjs/tween.js';
import vertexShader from './vertexShader.js';

const branchImageGeometry = new THREE.PlaneGeometry(8, 8);
const textMaterial = new THREE.MeshBasicMaterial({
	color: 0xffffff,
	side: THREE.DoubleSide,
});

class Branch {
	constructor(mapBranch, parent, parentNode) {
		this.mapBranch = mapBranch;
		this.parent = parent;
		this.parentNode = parentNode;
		this.textureLoader = new THREE.TextureLoader();
	}

	async init() {
		this.createMainTextMesh();
		this.createDescriptionTextMesh();
		await this.createMeshImages();
	}

	createMainTextMesh() {
		const textGeometry = new TextGeometry(
			this.mapBranch.text.toUpperCase(),
			{
				font: this.parent.font,
				size: 1,
				height: 0.1,
				curveSegments: 12,
				bevelEnabled: false,
			}
		);

		const textMesh = new THREE.Mesh(textGeometry, textMaterial);
		textMesh.scale.set(0, 0, 0);
		textMesh.position.copy(this.mapBranch.position);

		this.parent.scene.add(textMesh);

		this.textMesh = textMesh;
	}

	createDescriptionTextMesh() {
		if (!this.mapBranch.description) return;

		const descriptionTextGeometry = new TextGeometry(
			this.mapBranch.description,
			{
				font: this.parent.font,
				size: 1,
				height: 0.1,
				curveSegments: 100,
				bevelEnabled: false,
			}
		);

		let descriptionTextMesh = new THREE.Mesh(
			descriptionTextGeometry,
			textMaterial
		);

		descriptionTextMesh.scale.set(0, 0, 0);
		descriptionTextMesh.position.copy(this.mapBranch.position);
		descriptionTextMesh.position.y -= 3;
		descriptionTextMesh.position.z += 3;

		this.parent.scene.add(descriptionTextMesh);

		this.descriptionTextMesh = descriptionTextMesh;
	}

	back() {
		this.descriptionTextMesh.position.copy(this.mapBranch.position);
		this.descriptionTextMesh.position.y -= 3;
		this.descriptionTextMesh.position.z += 3;

		new TWEEN.Tween(this.textMesh.scale)
			.to({ x: 1, y: 1, z: 1 }, 500)
			.easing(TWEEN.Easing.Quadratic.Out)
			.start();

		new TWEEN.Tween(this.parentNode.textMesh.scale)
			.to({ x: 1, y: 1, z: 1 }, 500)
			.easing(TWEEN.Easing.Quadratic.Out)
			.start();

		const aspectRatio = this.parentNode.getAspectRatio();

		new TWEEN.Tween(this.parentNode.mesh.scale)
			.to({ x: aspectRatio, y: 1, z: 1 }, 500)
			.easing(TWEEN.Easing.Quadratic.Out)
			.start();

		for (const branch of this.parentNode.branches) {
			if (branch !== this) {
				new TWEEN.Tween(branch.descriptionTextMesh.scale)
					.to({ x: 1, y: 1, z: 1 }, 500)
					.easing(TWEEN.Easing.Quadratic.Out)
					.start();
			}

			new TWEEN.Tween(branch.textMesh.scale)
				.to({ x: 1, y: 1, z: 1 }, 500)
				.easing(TWEEN.Easing.Quadratic.Out)
				.start();

			for (const meshImage of branch.meshImages) {
				const aspectRatio =
					meshImage.material.map.image.width /
					meshImage.material.map.image.height;
				new TWEEN.Tween(meshImage.scale)
					.to({ x: aspectRatio, y: 1, z: 1 }, 500)
					.easing(TWEEN.Easing.Quadratic.Out)
					.start();
			}
		}
	}

	onClick() {
		this.parent.selectedBranch = this;
		const cameraPos = this.parent.camera.position;

		this.descriptionTextMesh.position.set(0, 0, cameraPos.z - 25);

		this.descriptionTextMesh.geometry.computeBoundingBox();
		const center = this.descriptionTextMesh.geometry.boundingBox.getCenter(
			new THREE.Vector3()
		);

		this.descriptionTextMesh.updateMatrixWorld();
		center.applyMatrix4(this.descriptionTextMesh.matrixWorld);

		this.descriptionTextMesh.position.sub(
			new THREE.Vector3(center.x, center.y, 0)
		);

		new TWEEN.Tween(this.textMesh.scale)
			.to({ x: 0, y: 0, z: 0 }, 500)
			.easing(TWEEN.Easing.Quadratic.Out)
			.start();

		new TWEEN.Tween(this.parentNode.textMesh.scale)
			.to({ x: 0, y: 0, z: 0 }, 500)
			.easing(TWEEN.Easing.Quadratic.Out)
			.start();

		new TWEEN.Tween(this.parentNode.mesh.scale)
			.to({ x: 0, y: 0, z: 0 }, 500)
			.easing(TWEEN.Easing.Quadratic.Out)
			.start();

		for (const branch of this.parentNode.branches) {
			if (branch !== this) {
				new TWEEN.Tween(branch.descriptionTextMesh.scale)
					.to({ x: 0, y: 0, z: 0 }, 500)
					.easing(TWEEN.Easing.Quadratic.Out)
					.start();
			}

			new TWEEN.Tween(branch.textMesh.scale)
				.to({ x: 0, y: 0, z: 0 }, 500)
				.easing(TWEEN.Easing.Quadratic.Out)
				.start();

			for (const meshImage of branch.meshImages) {
				new TWEEN.Tween(meshImage.scale)
					.to({ x: 0, y: 0, z: 0 }, 500)
					.easing(TWEEN.Easing.Quadratic.Out)
					.start();
			}
		}
	}

	onAnimate(t) {
		if (!this.meshImages) return;

		const cameraPos = this.parent.camera.position;

		for (const meshImage of this.meshImages) {
			meshImage.lookAt(cameraPos);
		}

		this.textMesh.lookAt(cameraPos);

		if (this.descriptionTextMesh) {
			this.descriptionTextMesh.lookAt(cameraPos);

			let intersects = this.parent.raycaster.intersectObject(
				this.descriptionTextMesh
			);

			if (intersects.length > 0) {
				if (this.parent.mouseDown) {
					this.onClick();
				}

				document.body.style.cursor = 'pointer';
				this.resetMouse = true;
			} else if (this.resetMouse) {
				document.body.style.cursor = 'default';
				this.resetMouse = false;
			}
		}
	}

	createMeshImage(image) {
		return new Promise((resolve) => {
			this.textureLoader.load(image, (texture) => {
				const material = new THREE.MeshBasicMaterial({
					map: texture,
					side: THREE.DoubleSide,
					opacity: 0,
					transparent: true,
				});

				const mesh = new THREE.Mesh(branchImageGeometry, material);

				let x, y, z;
				let overlapping = true;
				let iterations = 0;

				while (overlapping && iterations < 100) {
					x = randInt(-20, 20) + this.mapBranch.position.x;
					y = randInt(-15, 15) + this.mapBranch.position.y;
					z = randInt(-5, 5) + this.mapBranch.position.z;

					overlapping = false;

					for (const meshImage of this.meshImages) {
						if (mesh.position.distanceTo(meshImage.position) < 10) {
							overlapping = true;
						}
					}

					iterations++;
				}

				mesh.position.set(x, y, z);

				const aspectRatio = texture.image.width / texture.image.height;
				mesh.scale.set(aspectRatio, 1, 1);

				this.parent.scene.add(mesh);

				this.meshImages.push(mesh);

				resolve();
			});
		});
	}

	async createMeshImages() {
		this.meshImages = [];

		for (const image of this.mapBranch.images) {
			await this.createMeshImage(image);
		}

		let averageX = 0;
		let averageY = 0;
		let averageZ = 0;

		for (const meshImage of this.meshImages) {
			averageX += meshImage.position.x;
			averageY += meshImage.position.y;
			averageZ += meshImage.position.z;
		}

		this.textMesh.position.y = averageY / this.meshImages.length;
		this.textMesh.position.z = averageZ / this.meshImages.length + 3;
		this.textMesh.position.x = averageX / this.meshImages.length;

		if (this.descriptionTextMesh) {
			this.descriptionTextMesh.position.copy(this.textMesh.position);
			this.descriptionTextMesh.position.y -= 5;
			this.descriptionTextMesh.position.z += 3;
		}
	}
}

export default Branch;
