class MindMap extends Component {
	constructor() {
		super();

		this.mindmapContainer = createRef();

		this.width = window.innerWidth;
		this.height = window.innerHeight;

		this.state = {};
	}

	initScene() {
		this.scene = new THREE.Scene();
	}

	initCamera() {
		this.camera = new THREE.PerspectiveCamera(
			75,
			this.width / this.height,
			0.1,
			1000
		);
		this.camera.position.z = 20;
	}

	initRenderer() {
		this.renderer = new THREE.WebGLRenderer();
		this.renderer.setSize(this.width, this.height);
		this.renderer.setPixelRatio(window.devicePixelRatio);

		let domElement = this.renderer.domElement;
		domElement.className = 'mindmap-canvas';
		this.mindmapContainer.current.appendChild(domElement);
	}

	initControls() {
		this.controls = new OrbitControls(
			this.camera,
			this.renderer.domElement
		);
		this.controls.enableZoom = false;
	}

	initRaycaster() {
		this.raycaster = new THREE.Raycaster();
		this.mouse = new THREE.Vector2();
	}

	loadModels() {
		const loader = new OBJLoader();

		return new Promise((resolve) => {
			loader.load('assets/models/hand.obj', (object) => {
				this.hand = object;

				object.position.set(0, 0, 0);
				this.scene.add(object);

				resolve();
			});
		});
	}

	onClick(node) {
		if (!this.state.loaded) return;

		this.setState({
			backButton: true,
		});

		this.controls.enableZoom = true;

		const aspectRatio = node.getAspectRatio();

		new TWEEN.Tween(this.camera.position)
			.to({ z: 65 }, 1000)
			.easing(TWEEN.Easing.Cubic.InOut)
			.start();

		new TWEEN.Tween(this.hand.scale)
			.to({ x: 0, y: 0, z: 0 }, 400)
			.easing(TWEEN.Easing.Quadratic.Out)
			.start();

		new TWEEN.Tween(node.mesh.position)
			.to({ x: 0, y: 0, z: 0 }, 400)
			.easing(TWEEN.Easing.Quadratic.Out)
			.start();

		new TWEEN.Tween(node.mesh.material.uniforms.uAmp)
			.to({ value: 0 }, 400)
			.easing(TWEEN.Easing.Quadratic.Out)
			.start();

		if (node.textMesh) {
			new TWEEN.Tween(node.textMesh.position)
				.to(
					{
						x: -5 * aspectRatio,
						y: -14,
						z: 4,
					},
					400
				)
				.easing(TWEEN.Easing.Quadratic.Out)
				.start();
		}

		for (const branch of node.branches) {
			if (branch.descriptionTextMesh) {
				new TWEEN.Tween(branch.descriptionTextMesh.scale)
					.to(
						{
							x: 1,
							y: 1,
							z: 1,
						},
						400
					)
					.easing(TWEEN.Easing.Quadratic.Out)
					.start();
			}

			new TWEEN.Tween(branch.textMesh.scale)
				.to(
					{
						x: 1,
						y: 1,
						z: 1,
					},
					400
				)
				.easing(TWEEN.Easing.Quadratic.Out)
				.start();
			branch.meshImages.forEach((meshImage) => {
				new TWEEN.Tween(meshImage.material)
					.to(
						{
							opacity: 1,
						},
						400
					)
					.easing(TWEEN.Easing.Quadratic.Out)
					.start();
			});
		}

		Object.values(this.nodes).forEach((otherNode) => {
			if (otherNode === node) {
				return;
			}

			if (otherNode.mesh) {
				new TWEEN.Tween(otherNode.mesh.scale)
					.to({ x: 0, y: 0, z: 0 }, 400)
					.easing(TWEEN.Easing.Quadratic.Out)
					.start();
			}

			if (otherNode.textMesh) {
				new TWEEN.Tween(otherNode.textMesh.scale)
					.to({ x: 0, y: 0, z: 0 }, 400)
					.easing(TWEEN.Easing.Quadratic.Out)
					.start();

				new TWEEN.Tween(node.textMesh.material)
					.to({ opacity: 1 }, 400)
					.easing(TWEEN.Easing.Quadratic.Out)
					.start();
			}
		});
	}

	loadFont() {
		const fontLoader = new FontLoader();

		return new Promise((resolve) => {
			fontLoader.load('assets/fonts/MadeBlack.json', (font) => {
				this.font = font;
				resolve();
			});
		});
	}

	loadMap() {
		this.nodes = [];
		this.raycastObjects = [];

		return new Promise(async (resolve) => {
			for (const [index, mapNode] of Object.entries(Object.values(map))) {
				const node = new Node(mapNode, index, this, map);

				this.nodes.push(node);
				await node.init();

				this.raycastObjects.push(node.mesh);
			}

			resolve();
		});
	}

	animate(t = 0) {
		requestAnimationFrame(this.animate.bind(this));
		this.renderer.render(this.scene, this.camera);

		TWEEN.update();
		stats.update();
		this.controls.update();

		if (this.hand) {
			this.hand.rotation.y += 0.001;
		}

		this.raycaster.setFromCamera(this.mouse, this.camera);

		this.getIntersects();
		this.animateObjects(t);
	}

	getIntersects() {
		if (!this.raycastObjects) return;

		this.intersects = this.raycaster.intersectObjects(this.raycastObjects);
	}

	animateObjects(t) {
		if (!this.nodes) return;

		for (const node of this.nodes) {
			node.onAnimate(t);
		}
	}

	resize() {
		this.width = window.innerWidth;
		this.height = window.innerHeight;
		this.camera.aspect = this.width / this.height;
		this.camera.updateProjectionMatrix();
		this.renderer.setSize(this.width, this.height);
	}

	initLighting() {
		let ambientLight = new THREE.AmbientLight(0xffffff, 0.5);
		this.scene.add(ambientLight);

		let directionalLight = new THREE.DirectionalLight(0xffffff, 0.5);

		directionalLight.position.set(0, 1, 0);
		this.scene.add(directionalLight);
	}

	onMouseMove(e) {
		this.mouse.x = (e.clientX / this.width) * 2 - 1;
		this.mouse.y = -(e.clientY / this.height) * 2 + 1;
	}

	onMouseDown() {
		this.mouseDown = true;
	}

	onMouseUp() {
		this.mouseDown = false;
	}

	setLoaded() {
		this.setState({
			loaded: true,
		});

		this.renderer.domElement.classList.add('mindmap-canvas-loaded');
	}

	async initCanvas() {
		if (!this.mindmapContainer.current) return;

		this.initScene();
		this.initCamera();
		this.initRenderer();
		this.initControls();
		this.initLighting();
		this.initRaycaster();

		this.animate();

		this.setState({
			loadState: 'Loading models...',
		});
		await this.loadModels();

		this.setState({
			loadState: 'Loading fonts...',
		});
		await this.loadFont();

		this.setState({
			loadState: 'Loading map...',
		});
		await this.loadMap();

		this.setState({
			loadState: 'Loaded all assets!',
		});

		this.resize();

		document.addEventListener('resize', this.resize.bind(this));
		document.addEventListener('mousemove', this.onMouseMove.bind(this));
		document.addEventListener('mousedown', this.onMouseDown.bind(this));
		document.addEventListener('mouseup', this.onMouseUp.bind(this));

		this.setLoaded();

		setTimeout(() => {
			new TWEEN.Tween(this.camera.position)
				.to({ z: 50 }, 1000)
				.easing(TWEEN.Easing.Cubic.InOut)
				.start();
		}, 3000);
	}

	back() {
		if (this.selectedBranch) {
			this.selectedBranch.back();
			this.selectedBranch = null;

			return;
		}

		this.setState({
			backButton: false,
		});

		this.controls.enableZoom = false;

		const aspectRatio = this.selectedNode.getAspectRatio();

		new TWEEN.Tween(this.camera.position)
			.to({ z: 50 }, 1000)
			.easing(TWEEN.Easing.Cubic.InOut)
			.start();

		new TWEEN.Tween(this.hand.scale)
			.to({ x: 1, y: 1, z: 1 }, 500)
			.easing(TWEEN.Easing.Quadratic.Out)
			.start();

		new TWEEN.Tween(this.selectedNode.mesh.position)
			.to(
				{
					x: this.selectedNode.mapNode.position.x,
					y: this.selectedNode.mapNode.position.y,
					z: this.selectedNode.mapNode.position.z,
				},
				500
			)
			.easing(TWEEN.Easing.Quadratic.Out)
			.start();

		new TWEEN.Tween(this.selectedNode.mesh.material.uniforms.uAmp)
			.to({ value: 0 }, 500)
			.easing(TWEEN.Easing.Quadratic.Out)
			.start();

		new TWEEN.Tween(this.selectedNode.textMesh.position)
			.to(
				{
					x: this.selectedNode.mapNode.position.x - 5 * aspectRatio,
					y: this.selectedNode.mapNode.position.y - 14,
					z: this.selectedNode.mapNode.position.z,
				},
				500
			)
			.easing(TWEEN.Easing.Quadratic.Out)
			.start();

		for (const branch of this.selectedNode.branches) {
			if (branch.descriptionTextMesh) {
				new TWEEN.Tween(branch.descriptionTextMesh.scale)
					.to(
						{
							x: 0,
							y: 0,
							z: 0,
						},
						500
					)
					.easing(TWEEN.Easing.Quadratic.Out)
					.start();
			}

			new TWEEN.Tween(branch.textMesh.scale)
				.to(
					{
						x: 0,
						y: 0,
						z: 0,
					},
					500
				)
				.easing(TWEEN.Easing.Quadratic.Out)
				.start();
			branch.meshImages.forEach((meshImage) => {
				new TWEEN.Tween(meshImage.material)
					.to(
						{
							opacity: 0,
						},
						500
					)
					.easing(TWEEN.Easing.Quadratic.Out)
					.start();
			});
		}

		Object.values(this.nodes).forEach((otherNode) => {
			if (otherNode === this.selectedNode) {
				return;
			}

			const aspectRatio = otherNode.getAspectRatio();

			if (otherNode.mesh) {
				new TWEEN.Tween(otherNode.mesh.scale)
					.to({ x: aspectRatio, y: 1, z: 1 }, 500)
					.easing(TWEEN.Easing.Quadratic.Out)
					.start();
			}

			if (otherNode.textMesh) {
				new TWEEN.Tween(otherNode.textMesh.scale)
					.to({ x: 1, y: 1, z: 1 }, 500)
					.easing(TWEEN.Easing.Quadratic.Out)
					.start();

				new TWEEN.Tween(this.selectedNode.textMesh.material)
					.to({ opacity: 0 }, 500)
					.easing(TWEEN.Easing.Quadratic.Out)
					.start();
			}
		});

		this.selectedNode = null;
	}

	componentDidMount() {
		this.initCanvas();
	}

	render() {
		return (
			<div ref={this.mindmapContainer} className='mindmap-container'>
				{this.state.loaded && (
					<Gradient
						gradients={gradients}
						property='text'
						element='h1'
						className='resistance-title'
						angle='90deg'>
						RESISTANCE
					</Gradient>
				)}
				{this.state.backButton && (
					<Gradient
						gradients={gradients}
						property='text'
						element='span'
						className='back-button'
						angle='90deg'
						onClick={this.back.bind(this)}>
						Back
					</Gradient>
				)}
				{!this.state.loaded && (
					<Gradient
						gradients={gradients}
						property='text'
						element='span'
						className='loading-title'
						angle='90deg'>
						{this.state.loadState}
					</Gradient>
				)}
			</div>
		);
	}
}

export default MindMap;
